import React from 'react';
import './Payment.scss';
import {Link} from 'react-router';
import Sidebar from '../../Sidebar/components/Sidebar.js';
import InviteFriends from '../../../Widgets/InviteFriends/components/InviteFriends.js';
import ReferFriends from '../../../Widgets/ReferFriends/components/ReferFriends.js';
import StripeUITest from '../../../Widgets/StripeUITest/components/StripeUITest.js';
import PaymentCard from '../../../Widgets/PaymentCard/components/PaymentCard.js';
import Header from '../../../Header/components/Header.js';
import {connect} from 'react-redux';
import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class Payment extends React.Component {
    constructor(props) {
        super(props);
    }

    onChange = () => {}

    goToIndex = () => {
        this.props.router.push('/');
    }

    render() {
        if(!this.props.auth.loggedIn){
          this.goToIndex();
        }
        return (
          <div className="user-payment">
            <Header goHome={this.goToIndex} isLoggedIn={true} />
            <Sidebar />
            <div className="notification-section">
              <div id="title1" className="title-text">Payment Method</div>
              <div className="card-section">
                <PaymentCard />
              </div>
            </div>
            <div className="stripe-section">
              <div id="title2" className="title-text">Replace Existing Card With A New Card</div>
              <div className="stripe-card-section">
                <StripeUITest isUser={true} />
              </div>
            </div>
          </div>
        );
      }
    }

const mapStateToProps = (state) => {
  console.log(state);
  return {auth: state.auth};
};

export default connect(mapStateToProps)(Payment);



